:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.slbOverlay, .slbWrapOuter, .slbWrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slbOverlay {
  z-index: 2000;
  opacity: .7;
  background-color: #000;
  animation: slbOverlay .5s;
  overflow: hidden;
}

.slbWrapOuter {
  z-index: 2010;
  overflow-x: hidden;
  overflow-y: auto;
}

.slbWrap {
  text-align: center;
  position: absolute;
}

.slbWrap:before {
  content: "";
  height: 100%;
  vertical-align: middle;
  display: inline-block;
}

.slbContentOuter {
  vertical-align: middle;
  box-sizing: border-box;
  z-index: 2020;
  text-align: left;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 1em;
  display: inline-block;
  position: relative;
}

.slbContentEl .slbContentOuter {
  padding: 5em 1em;
}

.slbContent {
  position: relative;
}

.slbContentEl .slbContent {
  background-color: #fff;
  animation: slbEnter .3s;
  box-shadow: 0 .2em 1em rgba(0, 0, 0, .4);
}

.slbImageWrap {
  animation: slbEnter .3s;
  position: relative;
}

.slbImageWrap:after {
  content: "";
  z-index: -1;
  background-color: #fff;
  display: block;
  position: absolute;
  top: 5em;
  bottom: 5em;
  left: 0;
  right: 0;
  box-shadow: 0 .2em 1em rgba(0, 0, 0, .6);
}

.slbDirectionNext .slbImageWrap {
  animation: slbEnterNext .4s;
}

.slbDirectionPrev .slbImageWrap {
  animation: slbEnterPrev .4s;
}

.slbImage {
  width: auto;
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 5em 0;
  line-height: 0;
  display: block;
}

.slbCaption {
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  color: #fff;
  color: rgba(255, 255, 255, .7);
  text-align: center;
  padding: .71429em 0;
  font-size: 1.4em;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.slbCloseBtn, .slbArrow {
  cursor: pointer;
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
}

.slbCloseBtn::-moz-focus-inner, .slbArrow::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.slbCloseBtn:hover, .slbArrow:hover {
  opacity: .5;
}

.slbCloseBtn:active, .slbArrow:active {
  opacity: .8;
}

.slbCloseBtn {
  width: 1.66667em;
  height: 1.66667em;
  color: #fff;
  color: rgba(255, 255, 255, .7);
  text-align: center;
  font-size: 3em;
  line-height: 1.66667em;
  animation: slbEnter .3s;
  position: absolute;
  top: 0;
  right: -.33333em;
}

.slbLoading .slbCloseBtn {
  display: none;
}

.slbLoadingText {
  color: #fff;
  color: rgba(255, 255, 255, .9);
  font-size: 1.4em;
}

.slbArrows {
  position: fixed;
  top: 50%;
  left: 0;
  right: 0;
}

.slbLoading .slbArrows {
  display: none;
}

.slbArrow {
  width: 5em;
  height: 10em;
  opacity: .7;
  text-indent: -999em;
  margin-top: -5em;
  position: absolute;
  top: 50%;
  overflow: hidden;
}

.slbArrow:before {
  content: "";
  border: .8em solid rgba(0, 0, 0, 0);
  margin: -.8em 0 0 -.8em;
  position: absolute;
  top: 50%;
  left: 50%;
}

.slbArrow.next {
  right: 0;
}

.slbArrow.next:before {
  border-left-color: #fff;
}

.slbArrow.prev {
  left: 0;
}

.slbArrow.prev:before {
  border-right-color: #fff;
}

.slbIframeCont {
  width: 80em;
  height: 0;
  margin: 5em 0;
  padding-top: 56.25%;
  overflow: hidden;
}

.slbIframe {
  width: 100%;
  height: 100%;
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 .2em 1em rgba(0, 0, 0, .6);
}

@keyframes slbOverlay {
  from {
    opacity: 0;
  }

  to {
    opacity: .7;
  }
}

@-webkit-keyframes slbEnter {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -1em, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
  }
}

@-moz-keyframes slbEnter {
  from {
    opacity: 0;
    -moz-transform: translate3d(0, -1em, 0);
  }

  to {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
  }
}

@keyframes slbEnter {
  from {
    opacity: 0;
    transform: translate3d(0, -1em, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes slbEnterNext {
  from {
    opacity: 0;
    -webkit-transform: translate3d(4em, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
  }
}

@-moz-keyframes slbEnterNext {
  from {
    opacity: 0;
    -moz-transform: translate3d(4em, 0, 0);
  }

  to {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
  }
}

@keyframes slbEnterNext {
  from {
    opacity: 0;
    transform: translate3d(4em, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes slbEnterPrev {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-4em, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
  }
}

@-moz-keyframes slbEnterPrev {
  from {
    opacity: 0;
    -moz-transform: translate3d(-4em, 0, 0);
  }

  to {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
  }
}

@keyframes slbEnterPrev {
  from {
    opacity: 0;
    transform: translate3d(-4em, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

